/**
 * 
 * @example
    class Test extends Listener {
      constructor () {
        super();
        this.val = 0
      }
      ins () {
        this.val++
        this.fire('ins', { value: this.val })
        return this
      }
      des () {
        this.val--
        this.fire('des', { value: this.val })
        return this
      }
    }
    const test = new Test()
    const func = test.on('ins', event => {
      console.log('ins', event.value)
    })
    test.once('des', event => {
      console.log('des', event.value)
    })
    test.ins().des().des()
    test.off('ins', func)
    test.ins()
    console.log(test.val)
    // output:
    //    ins 1
    //    des 0
    //    0
 */
export const Listener = (function() {

  //#region 

  /**
   * @type { WeakMap<object, Object<string, Function> }
   */
  const _eventPool = new WeakMap()

  //#endregion

  ///#region 

  class __Listener__ {

    //#region 

    constructor () {
      _eventPool.set(this, {})
    }

    //#endregion

    //#region 

    /**
     * 
     * @param { string } name 
     * @param { Function } fn 
     * @param { * } scope 
     * @returns { Function } 
     */
    on (name, fn, scope) {
      if (typeof name !== 'string') {
        throw new Error('first param must be string')
      } else if (typeof fn !== 'function') {
        throw new Error('second param must be function')
      }
      name = name.toLowerCase()
      const eventPool = _eventPool.get(this)
      eventPool[name] || (eventPool[name] = [])
      eventPool[name].push(scope ? [fn, scope] : [fn])
      return fn
    }

    /**
     * 
     * @param { string } name 
     * @param { * } data 
     */
    fire (name, data) {
      if (typeof name !== 'string') {
        throw new Error('first param must be string')
      }
      name = name.toLowerCase()
      const eventArr = _eventPool.get(this)[name]
      if (eventArr) {
        const event = Object.assign({
          name, // 
          origin: this, // 
        }, data)
        const len = eventArr.length
        for (let i = 0; i < eventArr.length; i++) {
          const item = eventArr[i]
          let fn = item[0]
          // event.scope = item[1] ?? {}
          event.scope = item[1] || {}
          fn(event)
          if (eventArr.length < len) {
            i--
          }
        }
      }
    }

    /**
   * 
   * @param { string } name 
   * @param { Function } fn null
   */
    off (name, fn) {
      if (typeof name !== 'string') {
        throw new Error('first param must be string')
      } else if (typeof fn !== 'function') {
        throw new Error('second param must be function')
      }
      name = name.toLowerCase()
      const eventArr = _eventPool.get(this)[name]
      if (!eventArr || eventArr.length === 0) {
        return
      }
      if (fn) {
        for (let i = 0; i < eventArr.length; i++) {
          if (fn === eventArr[i][0]) {
            eventArr.splice(i, 1)
            i-- // 
          }
        }
      } else {
        eventArr.splice(0, eventArr.length)
      }
    }

    /**
   * 
   * @param { string } name 
   * @param { () => void } fn 
   * @param { * } scope 
   * @returns { () => * } 
   */
    once (name, fn, scope) {
      const _self = this
      function nfn () {
        _self.off(name, nfn)
        fn.apply(scope || _self, arguments)
      }
      this.on(name, nfn, scope)
      return fn
    }

    //#endregion

  }

  //#endregion

  return __Listener__

}())
